import React, { useEffect } from 'react';
import { useSearchParams } from 'react-router-dom';
import { Box, Typography, Button, Container } from '@mui/material';

const RedirectHandler: React.FC = () => {
  const [searchParams] = useSearchParams();
  const webUrl = searchParams.get('web');
  const platform = searchParams.get('platform');
  const id = searchParams.get('id');

  useEffect(() => {
    const redirect = async () => {
      try {
        if (!webUrl || !platform || !id) {
          throw new Error('Missing required parameters');
        }

        if (platform === 'youtube') {
          const isShorts = webUrl.includes('/shorts/');
          
          // Try to open YouTube app
          if (isShorts) {
            window.location.href = `vnd.youtube://shorts/${id}`;
            setTimeout(() => {
              window.location.href = `youtube://shorts/${id}`;
              setTimeout(() => {
                window.location.href = webUrl;
              }, 1000);
            }, 1000);
          } else {
            window.location.href = `vnd.youtube://watch?v=${id}`;
            setTimeout(() => {
              window.location.href = `youtube://watch?v=${id}`;
              setTimeout(() => {
                window.location.href = webUrl;
              }, 1000);
            }, 1000);
          }
        } else if (platform === 'instagram') {
          window.location.href = `instagram://media?id=${id}`;
          setTimeout(() => {
            window.location.href = webUrl;
          }, 2000);
        }
      } catch (error) {
        console.error('Redirect error:', error);
      }
    };

    redirect();
  }, [webUrl, platform, id]);

  return (
    <Container maxWidth="sm">
      <Box
        sx={{
          mt: 8,
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          p: 3,
          bgcolor: 'background.paper',
          borderRadius: 1,
          boxShadow: 3,
        }}
      >
        <Typography variant="h5" component="h1" gutterBottom>
          Opening App...
        </Typography>
        <Typography variant="body1" color="text.secondary" align="center" sx={{ mb: 3 }}>
          If nothing happens, click the button below to open in browser
        </Typography>
        <Button
          variant="contained"
          color="primary"
          href={webUrl || '#'}
          target="_blank"
          rel="noopener noreferrer"
        >
          Open in Browser
        </Button>
      </Box>
    </Container>
  );
};

export default RedirectHandler; 